import React, { useState } from "react";
import PropTypes from "prop-types";
import { Form, FormLabel, Input } from "./ContactForm.styled";
import { nanoid } from "nanoid";
import { Notify } from "notiflix/build/notiflix-notify-aio";

const ContactForm = ({ onAdd, onCheckUnique }) => {
  // const [name, setName] = useState("");
  // const [number, setNumber] = useState("");
  const [form, setForm] = useState({
    name: "",
    number: "",
  });

  //     onChange
  const handleChangeForm = ({ target }) => {
    const { name, value } = target;
    //   switch
    // switch (name) {
    //   case "name":
    //     setName(value);
    //     break;
    //   case "number":
    //     setNumber(value);
    //     break;
    //   default:
    //     break;
    // }
    //         -  
    setForm((prevForm) => ({ ...prevForm, [name]: value }));
  };

  //   submit 
  const handleFormSubmit = (e) => {
    e.preventDefault();
    //   useState  (  )
    const isValidateForm = validateForm();
    //       
    if (!isValidateForm) return;
    //      onAdd       name, number
    onAdd({ id: nanoid(10), ...form });
    resetForm();
  };
  //  
  const validateForm = () => {
    //       
    if (!name || !number) {
      Notify.failure("Some field is empty");
      return false;
    }
    //         
    return onCheckUnique(name);
  };
  //      
  // const resetForm = () => [setName(""), setNumber("")];
  const resetForm = () => setForm({ name: "", number: "" });

  //   state   
  const { name, number } = form;
  return (
    <Form onSubmit={handleFormSubmit}>
      <FormLabel>
        Name
        <Input
          type="text"
          name="name"
          placeholder="Enter name"
          value={name}
          onChange={handleChangeForm}
          pattern="^[a-zA-Z--]+(([' -][a-zA-Z-- ])?[a-zA-Z--]*)*$"
          title="Name may contain only letters, apostrophe, dash and spaces. For example Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan"
          required
        />
      </FormLabel>
      <FormLabel>
        Number
        <Input
          type="tel"
          name="number"
          placeholder="Enter phone number"
          value={number}
          onChange={handleChangeForm}
          pattern="\+?\d{1,4}?[-.\s]?\(?\d{1,3}?\)?[-.\s]?\d{1,4}[-.\s]?\d{1,4}[-.\s]?\d{1,9}"
          title="Phone number must be digits and can contain spaces, dashes, parentheses and can start with +"
          required
        />
      </FormLabel>
      <button type="submit">Add contact</button>
    </Form>
  );
};

ContactForm.propType = {
  value: PropTypes.string.isRequired,
  id: PropTypes.string.isRequired,
  onChange: PropTypes.func.isRequired,
};

export default ContactForm;
